import React, { useState } from "react";
import { Link } from "react-router-dom";
import { FiArrowLeft } from "react-icons/fi";

import { Container } from './styles'
import { NotificationManager } from "react-notifications";
import { apiAuth } from "../../services/api";

export default function NewCategoryAttribute({ history }) {
  const [title, setTitle] = useState("")

  async function handleSubmit(e) {
    e.preventDefault()

    if (!title) return NotificationManager.warning('Preencha todos os campos')

    const categoryAttribute = { title }
    try {
      await apiAuth.post('/categoryAttribute', categoryAttribute)
      NotificationManager.success('Clique aqui para voltar para a Dashboard', 'Cadastrado com sucesso', 10000, () => {
        history.push('/categoryAttribute')
      });
      setTitle("")
      document.querySelector("#newGun").reset()
    } catch (err) {
      NotificationManager.error('Erro')
      console.log(err)
    }

  }

  return (
    <Container>
      <div className="content">
        <section>
          <h1>Cadastrar uma nova categoria para attributos</h1>
          <Link to="/categoryAttribute" className="back-link">
            <FiArrowLeft size={18} color="#333" />
            Voltar
          </Link>
        </section>
        <form onSubmit={handleSubmit} id="newGun">
          <input
            placeholder="Titulo"
            value={title}
            onChange={e => setTitle(e.target.value)}
          />
          <button className="button" type="submit">
            Cadastrar
          </button>
        </form>
      </div>
    </Container>
  );
}
