import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiPlusCircle } from "react-icons/fi";
import { Container } from './styles'
import { apiAuth } from '../../services/api'
import AttributeComponent from "../../components/AttributeComponent";
import Header from '../../components/Header'

export default function Attribute({ history }) {
  const [attributes, setAttribute] = useState([])

  async function getAttributes() {
    const response = await apiAuth.get('attribute')
    setAttribute(response.data)
  }

  useEffect(() => {
    getAttributes()
  }, [])
  return (
    <Container>
      <Header className="content" />
      <div className="content">
        <h1>
          Atributos cadastrados
        <Link to="/attribute/add">
            <FiPlusCircle size={18} color="#000" />
          </Link>
        </h1>
        <AttributeComponent attributes={attributes} getAttributes={getAttributes} />
      </div>
    </Container>
  );
}
