import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiPlusCircle } from "react-icons/fi";
import { Container } from './styles'
import { apiAuth } from '../../services/api'
import CategoryAttributeComponent from "../../components/CategoryAttributeComponent";
import Header from "../../components/Header";

export default function CategoryAttribute() {
  const [categoryAttribute, setCategoryAttribute] = useState([])

  async function getCategoryAttribute() {
    const response = await apiAuth.get('categoryAttribute')
    setCategoryAttribute(response.data)
  }

  useEffect(() => {
    getCategoryAttribute()
  }, [])
  return (
    <Container>
      <Header />
      <div className="content">
        <h1>
          Categorias de Atributos
        <Link to="/categoryAttribute/add">
            <FiPlusCircle size={18} color="#000" />
          </Link>
        </h1>
        <CategoryAttributeComponent categoryAttribute={categoryAttribute} getCategoryAttribute={getCategoryAttribute} />
      </div>
    </Container>
  );
}
