import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiLogIn } from "react-icons/fi";
import { NotificationManager } from 'react-notifications';

import { Container } from './styles'

import logoImg from "../../assets/logo.png";
import { isAuthenticated, login } from "../../utils/auth";
import api from "../../services/api";

export default function SignIn({ history }) {

  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  useEffect(function () {
    if (isAuthenticated()) {
      history.push('/dashboard')
    }
  }, [history])

  const handleSubmit = async (e) => {
    e.preventDefault()

    if (!email || !password) return NotificationManager.warning('Email or password not provide')
    try {
      const { data: { token } } = await api.post('user/login', { email, password })

      login(token)
      history.push('/dashboard')
    } catch (err) {
      NotificationManager.warning('Invalid credentials');
    }
  }

  return (
    <Container>
      <section>
        <img src={logoImg} alt="" width="100%" />
        <form onSubmit={handleSubmit}>
          <h1>Faça seu logon</h1>
          <input
            placeholder="Seu email"
            value={email}
            onChange={({ target: { value } }) => setEmail(value)}
          />
          <input
            placeholder="Sua Senha"
            type="password"
            value={password}
            onChange={e => setPassword(e.target.value)}
          />
          <button type="submit" className="button">
            Entrar
          </button>

          <Link to="/register" className="back-link">
            <FiLogIn size={16} color="#000" />
            Não tenho cadastro
          </Link>
        </form>
      </section>
    </Container>
  );
}
