import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { FiArrowLeft } from "react-icons/fi";

import 'react-widgets/dist/css/react-widgets.css';
import { Container } from './styles'
import { NotificationManager } from "react-notifications";
import { apiAuth } from "../../services/api";
import { Multiselect } from 'react-widgets'

export default function GunAttribute({ history }) {
  const [gun, setGun] = useState({})
  const [category, setCategory] = useState("")
  const [attribute, setAttribute] = useState([])
  const { id } = useParams()

  async function handleSubmit(e) {
    e.preventDefault()

    if (!category) return NotificationManager.warning('Preencha todos os campos')
    return
    const attribute = { category }
    try {
      // await apiAuth.post('/gun', gun)
      NotificationManager.success('Clique aqui para voltar para a Dashboard', 'Cadastrado com sucesso', 10000, () => {
        history.push('/dashboard')
      });
      setCategory([])
      document.querySelector("#newGun").reset()
    } catch (err) {
      NotificationManager.error('Erro')
      console.log(err)
    }
  }

  useEffect(() => {
    async function getGun() {
      const response = await apiAuth.get('/gun/' + id)

      setGun(response.data)
    }
    async function getAttribute() {
      const response = await apiAuth.get('/attribute')

      setAttribute(response.data)
    }
    getGun()
    getAttribute()
  }, [])

  return (
    <Container>

      <div className="content">
        <section>
          <h1>Adicionar atributos a Arma {gun.title}</h1>

          <Link to="/gun" className="back-link">
            <FiArrowLeft size={18} color="#333" />
          Voltar para home
        </Link>
        </section>
        <form onSubmit={handleSubmit} id="newGun">
          <Multiselect
            data={attribute}
            textField="title"
            onChange={(ev) => {
              console.log(ev)
            }}
            placeholder="Selecione os atributos"
          />
          <button className="button" type="submit">
            Cadastrar
        </button>
        </form>
      </div>
    </Container>
  );
}
