import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiPlusCircle } from "react-icons/fi";
import { Container } from './styles'
import { apiAuth } from '../../services/api'
import CategoryGunComponent from "../../components/CategoryGunComponent";
import Header from "../../components/Header";

export default function CategoryGun() {
  const [categoryGuns, setCategoryGuns] = useState([])

  async function getCategoryGuns() {
    const response = await apiAuth.get('categoryGun')
    setCategoryGuns(response.data)
  }

  useEffect(() => {
    getCategoryGuns()
  }, [])
  return (
    <Container>
      <Header />
      <div className="content">
        <h1>
          Categorias de Armas
        <Link to="/categoryGun/add">
            <FiPlusCircle size={18} color="#000" />
          </Link>
        </h1>
        <CategoryGunComponent categoryGuns={categoryGuns} getCategoryGuns={getCategoryGuns} />
      </div>
    </Container>
  );
}
