import React, { useState } from 'react'
import { FiTrash2 } from "react-icons/fi";
import { apiAuth } from '../services/api';

export default function CategoryAttributeComponent(props) {
  let { categoryAttribute, getCategoryAttribute } = props
  const categoryAttributeComponent = categoryAttribute.map(category => <CategoryAttribute getCategoryAttribute={getCategoryAttribute} categoryAttribute={category} key={category._id} />)

  return <>{categoryAttributeComponent.length > 0 ? <ul>{categoryAttributeComponent}</ul> : <h2>Nenhuma cadastrada</h2>}</>
}

function CategoryAttribute(props) {
  const { getCategoryAttribute, categoryAttribute } = props

  const [editTitle, setEditTitle] = useState(false)
  const [field, setField] = useState("")

  async function handleRemove(categoryAttribute) {

    const confirm = window.confirm('Deseja remover?')

    if (confirm) {
      await apiAuth.delete(`categoryAttribute/${categoryAttribute._id}`)
      getCategoryAttribute()
    }
  }

  async function editField(categoryAttribute, _field) {
    let newCategoryAttribute = _field

    if (field !== categoryAttribute.title) {
      await apiAuth.put(`categoryAttribute/${categoryAttribute._id}`, newCategoryAttribute)
      getCategoryAttribute()
    }
    setEditTitle(false)
    setField("")
  }

  function doubleClick(field, callback) {
    callback()
    setField(field)
  }

  return (
    <li>
      <strong>Nome:</strong>
      {editTitle ?
        <input
          onBlur={() => editField(categoryAttribute, { title: field })}
          value={field}
          onChange={({ target: { value } }) => setField(value)}
        /> :
        <p
          onDoubleClick={() => doubleClick(categoryAttribute.title, () => setEditTitle(!editTitle))}
        >{categoryAttribute.title}</p>}
      {/* <strong>Criada em:</strong>
      <p>{gun.createdAt}</p>
      <strong>Atualizada em:</strong>
      <p>{gun.updatedAt}</p> */}

      <button type="button" onClick={() => handleRemove(categoryAttribute)}>
        <FiTrash2 size={20} color="#333" />
      </button>
    </li>
  )
}