import React from "react";

import PrivateRoute from './routes/PrivateRoute'
import PublicRoute from './routes/PublicRoute'

import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import SignIn from "./pages/SignIn";
import Register from "./pages/Register";
import Gun from "./pages/Gun";
import Attribute from "./pages/Attribute";
import NewAttribute from "./pages/NewAttribute";
import GunAttribute from "./pages/GunAttribute";
import CategoryGun from "./pages/CategoryGun";
import CategoryAttribute from "./pages/CategoryAttribute";
import NewGun from "./pages/NewGun";
import NewCategoryGun from "./pages/NewCategoryGun";
import history from "./utils/history";
import { logout } from "./utils/auth";
import Dashboard from "./pages/Dashboard";
import NewCategoryAttribute from "./pages/NewCategoryAttribute";

export default function Routes() {
  return (
    <BrowserRouter>
      <Switch history={history}>
        <Route path="/" component={Home} exact />
        <Route path="/logout" component={Logout} exact />
        <PrivateRoute component={Dashboard} path="/dashboard" exact />
        <PrivateRoute component={Gun} path="/gun" exact />
        <PrivateRoute component={Attribute} path="/attribute" exact />
        <PrivateRoute component={NewAttribute} path="/attribute/add" exact />
        <PrivateRoute component={NewGun} path="/gun/add" exact />
        <PrivateRoute component={CategoryGun} path="/categoryGun" exact />
        <PrivateRoute component={NewCategoryGun} path="/categoryGun/add" exact />
        <PrivateRoute component={CategoryAttribute} path="/categoryAttribute" exact />
        <PrivateRoute component={NewCategoryAttribute} path="/categoryAttribute/add" exact />
        <PrivateRoute component={GunAttribute} path="/gun/:id" />
        <PublicRoute restricted={true} component={SignIn} path="/signin" exact />
        <PublicRoute restricted={true} component={Register} path="/register" exact />
        <Route component={Notfound} />
      </Switch>
    </BrowserRouter>
  );
}

const Notfound = () => {
  return (
    <h1>Not Found</h1>
  )
}

const Home = () => {
  return <Redirect to="/dashboard" />
}

const Logout = ({ history }) => {
  logout().then(() => {
    history.push('/')
  }).catch(() => {
    history.push('/')
  })
  return (<h1>Tchau</h1>)
}