import React, { useState, useEffect } from 'react'
import { FiTrash2 } from "react-icons/fi";
import { apiAuth } from '../services/api';
import styled from 'styled-components';

export default function AttributeComponent(props) {
  let { attributes, getAttributes } = props
  const attributeComponent = attributes.map(attribute => <Attribute getAttributes={getAttributes} attribute={attribute} key={attribute._id} />)

  return <>{attributeComponent.length > 0 ? <AttributeContainer>{attributeComponent}</AttributeContainer> : <h2>Nenhum cadastrado</h2>}</>
}

function Attribute(props) {
  const { getAttributes, attribute } = props

  const [editTitle, setEditTitle] = useState(false)
  const [editCategory, setEditCategory] = useState(false)
  const [field, setField] = useState("")
  const [categories, setCategories] = useState([])


  useEffect(() => {
    async function getCategories() {
      const response = await apiAuth.get('/categoryAttribute')

      setCategories(response.data)
    }
    if (editCategory) getCategories()
  }, [editCategory])

  async function handleRemove(attribute) {

    const confirm = window.confirm('Deseja remover?')

    if (confirm) {
      await apiAuth.delete(`attribute/${attribute._id}`)
      getAttributes()
    }
  }

  async function editField(attribute, _field) {
    let newAttribute = _field

    if (field !== attribute.title) {
      await apiAuth.put(`attribute/${attribute._id}`, newAttribute)
      getAttributes()
    }
    setEditTitle(false)
    setEditCategory(false)
  }

  function doubleClick(field, callback) {
    callback()
    setField(field)
  }

  return (
    <li>
      <strong>Nome:</strong>
      {editTitle ?
        <input
          onBlur={() => editField(attribute, { title: field })}
          value={field}
          onChange={({ target: { value } }) => setField(value)}
        /> :
        <p
          onDoubleClick={() => doubleClick(attribute.title, () => setEditTitle(!editTitle))}
        >{attribute.title}</p>}
      <strong>Categoria:</strong>
      {editCategory ?
        <select
          onChange={e => { setField(e.target.value); console.log(e.target.value) }}
          onBlur={() => { editField(attribute, { category: field }); setEditCategory(!editCategory) }}
          value={field}
        >
          {!attribute.category && <option>Selecione</option>}
          {categories.map(elm => (
            <option
              value={elm._id}
              key={elm._id}
            >{elm.title}</option>
          ))}
        </select> :
        <p
          onDoubleClick={() => doubleClick(attribute.category?._id ? attribute.category?._id : '', () => setEditCategory(!editCategory))}
        >{attribute.category?.title ? attribute.category?.title : 'Sem categoria'}</p>}
      {/* <strong>Criada em:</strong>
      <p>{gun.createdAt}</p>
      <strong>Atualizada em:</strong>
      <p>{gun.updatedAt}</p> */}

      <button type="button" onClick={() => handleRemove(attribute)}>
        <FiTrash2 size={20} color="#333" />
      </button>
    </li>
  )
}

const AttributeContainer = styled.ul`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
  list-style: none;

  li {
    background: #d4d4da;
    padding: 24px;
    border-radius: 8px;
    position: relative;
    border: 1px solid transparent;
    transition: border-color 0.2s;

    input, select {
      height: 30px;
      padding: 0 10px;
    }
    
    button {
      position: absolute;
      right: 24px;
      top: 24px;
      background: none;
      border: 0;
    }
    
    button:hover {
      opacity: 0.8;
      border: 1px solid #fff;
    }
    
    strong {
      display: block;
      margin-bottom: 16px;
      color: #41414d;
    }

    p + strong {
      margin-top: 32px;
    }

    input + strong, select + strong {
      margin-top: 23px;
    }

    p {
      color: #737380;
      line-height: 21px;
      font-size: 16px;
    }
  }
`