import { createGlobalStyle } from 'styled-components'
import bg from './assets/bg.jpg'
export const GlobalStyle = createGlobalStyle`
@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap");

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px "Roboto", sans-serif;
  color: #333;
  background: #f0f0f5;
  background-image: url(${bg});
  -webkit-font-smoothing: antialiased;
}

input,
button,
textarea {
  font: 400 14px "Roboto", sans-serif;
}

button {
  cursor: pointer;
  background: #191919;
  border: 0;
}

input, select {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

form textarea {
  width: 100%;
  resize: vertical;
  min-height: 140px;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  padding: 16px 24px;
  border-radius: 8px;
  line-height: 24px;
}

.button {
  width: 100%;
  height: 60px;
  background: #191919;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;
  transition: filter 0.2s;
}

.button:hover {
  filter: brightness(90%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #333;
  font-size: 18px;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link svg {
  margin-right: 8px;
}

.back-link:hover {
  opacity: 0.8;
}

`