import styled from 'styled-components'

export const Container = styled.div`
  width: 100%;
  max-width: 1180px;
  padding: 0 30px;
  margin: 32px auto;

  .content {
    width: 100%;
    padding: 15px;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    /* display: flex; */
    justify-content: space-between;
    align-items: center;
  }

  .content + .content {
    margin-top: 30px;
  }

  h1 {
    margin-bottom: 24px;

    svg {
      margin-left: 15px;
    }
  }
  ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px;
    list-style: none;
 
    li {
      background: #d4d4da;
      padding: 24px;
      border-radius: 8px;
      position: relative;
      border: 1px solid transparent;
      transition: border-color 0.2s;

      input, select {
        height: 30px;
        padding: 0 10px;
      }
      
      button {
        position: absolute;
        right: 24px;
        top: 24px;
        background: none;
        border: 0;
      }
      
      button:hover {
        opacity: 0.8;
        border: 1px solid #fff;
      }
      
      strong {
        display: block;
        margin-bottom: 16px;
        color: #41414d;
      }
  
      p + strong {
        margin-top: 32px;
      }

      input + strong, select + strong {
        margin-top: 23px;
      }
  
      p {
        color: #737380;
        line-height: 21px;
        font-size: 16px;
      }
    }
  
    li:hover {
      border-color: #191919;
    }

  }

`