import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiArrowLeft } from "react-icons/fi";

import { Container } from './styles'
import { NotificationManager } from "react-notifications";
import { apiAuth } from "../../services/api";

export default function NewGun({ history }) {
  const [title, setTitle] = useState("")
  const [category, setCategory] = useState("")
  const [categories, setCategories] = useState([])

  async function handleSubmit(e) {
    e.preventDefault()

    if (!title || !category) return NotificationManager.warning('Preencha todos os campos')

    const gun = { title, category }
    try {
      await apiAuth.post('/gun', gun)
      NotificationManager.success('Clique aqui para voltar para a Dashboard', 'Cadastrado com sucesso', 10000, () => {
        history.push('/dashboard')
      });
      setTitle("")
      setCategory([])
      document.querySelector("#newGun").reset()
    } catch (err) {
      NotificationManager.error('Erro')
      console.log(err)
    }

  }

  useEffect(() => {
    async function getCategories() {
      const response = await apiAuth.get('/categoryGun')

      setCategories(response.data)
    }
    getCategories()
  }, [category])

  return (
    <Container>
      <div className="content">
        <section>
          <h1>Cadastrar uma nova arma</h1>

          <Link to="/gun" className="back-link">
            <FiArrowLeft size={18} color="#333" />
            Voltar para home
          </Link>
        </section>
        <form onSubmit={handleSubmit} id="newGun">
          <input
            placeholder="Titulo"
            value={title}
            onChange={e => setTitle(e.target.value)}
          />
          <select
            onChange={e => setCategory(e.target.value)}
          >
            <option>Selecione uma categoria</option>
            {categories.map(elm => (
              <option value={elm._id} key={elm._id}>{elm.title}</option>
            ))}
          </select>
          <button className="button" type="submit">
            Cadastrar
          </button>
        </form>
      </div>
    </Container>
  );
}
