import styled from 'styled-components'

export const Container = styled.div`
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;

  section {
    width: 100%;
    max-width: 350px;
    justify-content: center;

    form {
      padding: 15px;
      background: #f0f0f5;
      box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
      border-radius: 8px;

      input + input {
        margin-top: 15px;
      }

      h1 {
        font-size: 32px;
        margin-bottom: 32px;
      }
    }
  }
`