import React, { useState } from 'react'
import { FiTrash2 } from "react-icons/fi";
import { apiAuth } from '../services/api';

export default function CategoryGunComponent(props) {
  let { categoryGuns, getCategoryGuns } = props
  const gunsComponent = categoryGuns.map(category => <CategoryGun getCategoryGuns={getCategoryGuns} categoryGun={category} key={category._id} />)

  return <>{gunsComponent.length > 0 ? <ul>{gunsComponent}</ul> : <h2>Nenhuma cadastrada</h2>}</>
}

function CategoryGun(props) {
  const { getCategoryGuns, categoryGun } = props

  const [editTitle, setEditTitle] = useState(false)
  const [field, setField] = useState("")

  async function handleRemove(categoryGun) {

    const confirm = window.confirm('Deseja remover?')

    if (confirm) {
      await apiAuth.delete(`categoryGun/${categoryGun._id}`)
      getCategoryGuns()
    }
  }

  async function editField(categoryGun, _field) {
    let newCategoryGun = _field

    if (field !== categoryGun.title) {
      await apiAuth.put(`categoryGun/${categoryGun._id}`, newCategoryGun)
      getCategoryGuns()
    }
    setEditTitle(false)
    setField("")
  }

  function doubleClick(field, callback) {
    callback()
    setField(field)
  }

  return (
    <li>
      <strong>Nome:</strong>
      {editTitle ?
        <input
          onBlur={() => editField(categoryGun, { title: field })}
          value={field}
          onChange={({ target: { value } }) => setField(value)}
        /> :
        <p
          onDoubleClick={() => doubleClick(categoryGun.title, () => setEditTitle(!editTitle))}
        >{categoryGun.title}</p>}
      {/* <strong>Criada em:</strong>
      <p>{gun.createdAt}</p>
      <strong>Atualizada em:</strong>
      <p>{gun.updatedAt}</p> */}

      <button type="button" onClick={() => handleRemove(categoryGun)}>
        <FiTrash2 size={20} color="#333" />
      </button>
    </li>
  )
}