import React, { useState, useEffect } from 'react'
import { FiTrash2 } from "react-icons/fi";
import { apiAuth } from '../services/api';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

export default function GunComponent(props) {
  let { guns, getGuns } = props
  const gunsComponent = guns.map(gun => <Gun getGuns={getGuns} gun={gun} key={gun._id} />)

  return <>{gunsComponent.length > 0 ? <GunContainer>{gunsComponent}</GunContainer> : <h2>Nenhuma cadastrada</h2>}</>
}

function Gun(props) {
  const { getGuns, gun } = props

  const [editTitle, setEditTitle] = useState(false)
  const [editCategory, setEditCategory] = useState(false)
  const [field, setField] = useState("")
  const [categories, setCategories] = useState([])


  useEffect(() => {
    async function getCategories() {
      const response = await apiAuth.get('/categoryGun')

      setCategories(response.data)
    }
    if (editCategory) getCategories()
  }, [editCategory])

  async function handleRemove(gun) {

    const confirm = window.confirm('Deseja remover?')

    if (confirm) {
      await apiAuth.delete(`gun/${gun._id}`)
      getGuns()
    }
  }

  async function editField(gun, _field) {
    let newGun = _field

    if (field !== gun.title) {
      await apiAuth.put(`gun/${gun._id}`, newGun)
      getGuns()
    }
    setEditTitle(false)
    setEditCategory(false)
  }

  function doubleClick(field, callback) {
    callback()
    setField(field)
  }

  return (
    <li>
      <strong>Nome:</strong>
      {editTitle ?
        <input
          onBlur={() => editField(gun, { title: field })}
          value={field}
          onChange={({ target: { value } }) => setField(value)}
        /> :
        <p
          onDoubleClick={() => doubleClick(gun.title, () => setEditTitle(!editTitle))}
        >{gun.title}</p>}
      <strong>Categoria:</strong>
      {editCategory ?
        <select
          onChange={e => { setField(e.target.value); console.log(e.target.value) }}
          onBlur={() => { editField(gun, { category: field }); setEditCategory(!editCategory) }}
          value={field}
        >
          {!gun.category && <option>Selecione</option>}
          {categories.map(elm => (
            <option
              value={elm._id}
              key={elm._id}
            >{elm.title}</option>
          ))}
        </select> :
        <p
          onDoubleClick={() => doubleClick(gun.category?._id ? gun.category?._id : '', () => setEditCategory(!editCategory))}
        >{gun.category?.title ? gun.category?.title : 'Sem categoria'}</p>}
      {/* <strong>Criada em:</strong>
      <p>{gun.createdAt}</p>
      <strong>Atualizada em:</strong>
      <p>{gun.updatedAt}</p> */}

      <button type="button" onClick={() => handleRemove(gun)}>
        <FiTrash2 size={20} color="#333" />
      </button>
      {/* <Link to={`gun/${gun._id}`}>
        Adicionar atributos
      </Link> */}
    </li>
  )
}

const GunContainer = styled.ul`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
  list-style: none;

  li {
    background: #d4d4da;
    padding: 24px;
    border-radius: 8px;
    position: relative;
    border: 1px solid transparent;
    transition: border-color 0.2s;

    input, select {
      height: 30px;
      padding: 0 10px;
    }
    
    button {
      position: absolute;
      right: 24px;
      top: 24px;
      background: none;
      border: 0;
    }
    
    button:hover {
      opacity: 0.8;
      border: 1px solid #fff;
    }
    
    strong {
      display: block;
      margin-bottom: 16px;
      color: #41414d;
    }

    p + strong {
      margin-top: 32px;
    }

    input + strong, select + strong {
      margin-top: 23px;
    }

    p {
      color: #737380;
      line-height: 21px;
      font-size: 16px;
    }
  }
`