import axios from "axios";
import { getToken } from "../utils/auth";

const API_URL = "https://loadout-caina.herokuapp.com/"

const api = axios.create({
  baseURL: API_URL
});

export const apiAuth = axios.create({
  baseURL: API_URL,
  headers: {
    Authorization: {
      toString() {
        return `Bearer ${getToken()}`
      }
    }
  }
})
export default api;
